import React from "react";
import phoneTwo from "./img/phoneTwopng.png"
import treeMainChange from "./img/treeMainChange.png"
import { motion } from "framer-motion"
import { useInView } from "react-intersection-observer";

export const MainComponent = () => {
    const [serviceRef, serviceInView] = useInView({
        triggerOnce: true,
        rootMargin: "-50px 0px",
    });

    const [aboutRef, aboutInView] = useInView({
        triggerOnce: true,
        rootMargin: "-50px 0px",
    });
    return (
        <main className="main">
            <section className="service">
                <div className="service__container">
                    <div className="service-block">
                        <motion.h2
                            ref={serviceRef}
                            initial={{ opacity: 0, x: -100 }}
                            animate={serviceInView ? { opacity: 1, x: 0 } : {}}
                            transition={{ duration: 0.5, delay: 0.1 }}
                            className="service-block__title">
                            Что мы предлагаем?
                        </motion.h2>
                        <motion.div
                            initial={{ opacity: 0, x: -100 }}
                            animate={serviceInView ? { opacity: 1, x: 0 } : {}}
                            transition={{ duration: 0.5, delay: 0.5 }}
                            className="service-block__img">
                            <img src={phoneTwo} alt="Комманда" />
                        </motion.div>
                    </div>
                    <div className="service-content">
                        <div className="service-content__block">
                            <motion.div
                                initial={{ opacity: 0, y: -50 }}
                                animate={serviceInView ? { opacity: 1, y: 0 } : {}}
                                transition={{ duration: 0.5, delay: 0.7 }}
                                className="service-content__number">01</motion.div>
                            <motion.h2
                                initial={{ opacity: 0, x: 30 }}
                                animate={serviceInView ? { opacity: 1, x: 0 } : {}}
                                transition={{ duration: 0.5, delay: 1.2 }}
                                className="service-content__title">Качество</motion.h2>
                            <motion.div
                                initial={{ opacity: 0, x: 30 }}
                                animate={serviceInView ? { opacity: 1, x: 0 } : {}}
                                transition={{ duration: 0.5, delay: 1.4 }}
                                className="service-content__text"><p>Наш упор мы ставим на качество и будь готова к тому, что столкнешься с популярностью.</p></motion.div>
                        </div>
                        <div className="service-content__block">
                            <motion.div
                                initial={{ opacity: 0, y: -50 }}
                                animate={serviceInView ? { opacity: 1, y: 0 } : {}}
                                transition={{ duration: 0.5, delay: 0.9 }}
                                className="service-content__number">02</motion.div>
                            <motion.h2
                                initial={{ opacity: 0, x: 30 }}
                                animate={serviceInView ? { opacity: 1, x: 0 } : {}}
                                transition={{ duration: 0.5, delay: 1.2 }}
                                className="service-content__title">Компетентность</motion.h2>
                            <motion.div
                                initial={{ opacity: 0, x: 30 }}
                                animate={serviceInView ? { opacity: 1, x: 0 } : {}}
                                transition={{ duration: 0.5, delay: 1.4 }}
                                className="service-content__text"><p>Мы предлагаем самую профессиональную работу чатеров и остальных участников нашей команды.</p></motion.div>
                        </div>
                        <div className="service-content__block">
                            <motion.div
                                initial={{ opacity: 0, y: -50 }}
                                animate={serviceInView ? { opacity: 1, y: 0 } : {}}
                                transition={{ duration: 0.5, delay: 1 }}
                                className="service-content__number">03</motion.div>
                            <motion.h2
                                initial={{ opacity: 0, x: 30 }}
                                animate={serviceInView ? { opacity: 1, x: 0 } : {}}
                                transition={{ duration: 0.5, delay: 1.2 }}
                                className="service-content__title">Комфорт</motion.h2>
                            <motion.div
                                initial={{ opacity: 0, x: 30 }}
                                animate={serviceInView ? { opacity: 1, x: 0 } : {}}
                                transition={{ duration: 0.5, delay: 1.4 }}
                                className="service-content__text"><p>Гибкий график, команда, бонусы и условия, которые вам понравятся</p></motion.div>
                        </div>
                    </div>
                </div>
            </section>
            <section className="about service">
                <div className="about__container service__container">
                    <div className="about-block service-block">
                        <motion.h2
                            ref={aboutRef}
                            initial={{ opacity: 0, x: -100 }}
                            animate={aboutInView ? { opacity: 1, x: 0 } : {}}
                            transition={{ duration: 0.5, delay: 0.1 }}
                            className="service-block__title about-block__title">
                            О нас
                        </motion.h2>
                        <motion.div
                            initial={{ opacity: 0, x: -100 }}
                            animate={aboutInView ? { opacity: 1, x: 0 } : {}}
                            transition={{ duration: 0.5, delay: 0.3 }}
                            className="about-block__text">
                            <p>
                                Мы не просто так называемся Lilith Agency, а все потому, что Лилит - это первая жена Адама, которая была выгнала из Эдема за то, что отказалась подчиняться мужу. Тут мы и ищем таких девушек как Лилит - сильных, независимых и индивидуальных и акцент в нашей работе мы делаем именно на желаниях модели!
                            </p>
                        </motion.div>
                    </div>
                    <div className="about-content service-content">
                        <motion.div
                            initial={{ opacity: 0, x: 100 }}
                            animate={aboutInView ? { opacity: 1, x: 0 } : {}}
                            transition={{ duration: 0.5, delay: 0.5 }}
                            className="about-content__img"><img src={treeMainChange} alt="Дерево Лилит" /></motion.div>
                    </div>
                </div>
            </section>
        </main>
    )
}