import React from "react";
import logo from "./img/logoTolyaTest.svg"
import { motion } from "framer-motion"
export const HeaderComponent = () => {
    return (
        <header className="header">
            <div className="header__container">
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 0.5, delay: 0.1 }}
                    className="header__logo">
                    <a href="">
                        <img style={{ width: "100px", height: "auto" }} className="header__logo-svg" src={logo} alt="" />
                    </a>
                </motion.div>
                <div className="header-block">
                    <h1 className="header-block__title">
                        <motion.span
                            initial={{ opacity: 0, x: -100 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ duration: 0.5, delay: 0.3 }}>
                            LILITH
                        </motion.span>
                        <motion.span
                            initial={{ opacity: 0, x: -100 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ duration: 0.5, delay: 0.6 }}>
                            AGENCY
                        </motion.span>
                    </h1>
                    <motion.h2
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ duration: 1, delay: 0.9 }}
                        className="header-block__text">Beauty meets success</motion.h2>
                </div>
            </div>
        </header >
    )
}